/* Border box reset from https://css-tricks.com/box-sizing/ */
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

html,
body {
  width: 100%;
  height: 100%;
  background: black;
}

.App {
  text-align: center;
}



